ul,li { list-style: none; }
a { text-decoration: none; color: #000; }
* {box-sizing: border-box; margin: 0; padding: 0; }
html {
}
body { margin: 0 auto; width: 100%; }
.container-wrap {
    margin: 70px 0 50px;
}
.mainTitle {
    position: relative;
    margin-bottom: 80px;
}
.mainTitle img, .mainBottom img {
    display: block;
    margin: 0 auto;
}
.mainBottom img {
    margin-top: 30px;
}
.categoriesWrap {
    width: 80%;
    margin: 0 auto;
}
.detailCover img {
    display: inherit;
    margin: 0 auto;
}
.detailWrapper {
    position: relative;
    overflow-x: hidden;
    overflow-y: hidden;
    width: 100vw;
}
.detailWrapBack {
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .9;
    filter: blur(10px);
    z-index: -1;
}
.detailWrap {
    margin: 50px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.items {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 15%;
    background: rgba(255, 255, 255, .2);
    border-radius: 0px;
}
.detailCover {
    width: 100%;
    height: 100%;
    flex: 50%;
    flex-grow: 0;
    margin: 50px;
}
.detailExplain {
    flex: 50%;
    flex-shrink: 0;
    margin: 50px;
}
.detailExplain h1 {
    margin: 0 0 15px 0;
    font-size: 42px;
}
.detailExplain h4 {
    font-size: 18px;
    margin-bottom: 20px;
    color: #2e2e2e;
}
.detailExplain span {
    display: inline-block;
    margin-top: 10px;
    font-size: 18px;
}
.detailExplain a {
    display: inline-block;
    border-radius: 15px;
    background: rgba(255, 255, 255, .7);
    margin-top: 40px;
    font-size: 20px;
    line-height: 50px;
    width: 105px;
    text-align: center;
    height: 50px;
}