.box-wrap {
    flex: 1;
    display: flex;
    justify-content: center;
    padding: 5px 5px 50px;
    border: 1px solid rgba(0, 0, 0, 0);
    /* background-color: #fff; */
    transition: .2s;
}
.box-wrap:hover {
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}
h1 {
    margin: 30px 0 15px 0;
    font-size: 28px;
}
.img-wrap {
    position: relative;
}
.lank-wrap {
    position: absolute;
    top: 0;
    margin: 15px;
    width: 40px;
    height: 40px;
    background: rgba(0, 0, 0, .4);
    border-radius: 50%;
    vertical-align: middle;
}
.lank-wrap p {
    color: rgba(255,255, 255, 1);
    font-weight: 700;
    font-size: 22px;
    line-height: 44px;
    text-align: center;
    vertical-align: middle;
}
.explan-box {
    margin: 10px 5px 0 5px;
}
h2 {
    margin-bottom: 10px;
}
.genre-wrap {
    display: flex;
    margin-bottom: 10px;
}
.genre-wrap li {
    background-color: #6ca2f494;
    border-radius: 10px;
    margin-right: 5px;
    padding: 5px;
    color: #f1f1f1;
    font-size: 14px;
    font-weight: 400;
}